// 自适应正则脚本
// 功能：检测AI输出聊天中存在表达式内容时，自动注册相应的正则规则
// 卸载时自动移除所有其注册的正则
// 使用聊天变量来记录已注册的正则ID缓存

import regexData from './regex.json';

$(async () => {
  console.info('自适应正则脚本已加载');

  // 聊天变量键名
  const CACHE_KEY = 'registered_regex_ids';

  // 获取缓存的已注册正则ID
  const getCachedRegexIds = (): Set<string> => {
    try {
      const cached = getVariables({ type: 'chat' });
      const ids = cached?.[CACHE_KEY];
      if (Array.isArray(ids)) {
        return new Set(ids);
      }
    } catch (e) {
      console.warn('获取缓存的正则ID失败:', e);
    }
    return new Set();
  };

  // 保存已注册的正则ID到聊天变量
  const saveCachedRegexIds = (ids: Set<string>): void => {
    try {
      replaceVariables({ [CACHE_KEY]: Array.from(ids) }, { type: 'chat' });
    } catch (e) {
      console.warn('保存正则ID缓存失败:', e);
    }
  };

  // 解析正则规则
  const parseRegexRules = (): TavernRegex[] => {
    return regexData
      .filter((rule: any) => !rule.disabled)
      .map((rule: any) => ({
        id: rule.id,
        script_name: rule.scriptName,
        enabled: true,
        scope: 'character' as const,
        find_regex: rule.findRegex,
        replace_string: rule.replaceString,
        trim_strings: Array.isArray(rule.trimStrings) ? rule.trimStrings.join('\n') : '',
        source: {
          user_input: false,
          ai_output: true,
          slash_command: false,
          world_info: false,
        },
        destination: {
          display: true,
          prompt: false,
        },
        run_on_edit: rule.runOnEdit ?? true,
        min_depth: rule.minDepth ?? null,
        max_depth: rule.maxDepth ?? 10,
      }));
  };

  // 提取检测模式
  const extractDetectionPatterns = (): { id: string; pattern: RegExp; name: string; quickCheck: string }[] => {
    return regexData
      .filter((rule: any) => !rule.disabled)
      .map((rule: any) => {
        let patternStr = rule.findRegex;
        const match = patternStr.match(/^\/(.+)\/([gimsuy]*)$/);
        if (match) {
          patternStr = match[1];
        }

        try {
          const pattern = new RegExp(patternStr, 'i');

          let quickCheck = '';
          if (patternStr.includes('{') || patternStr.includes('\\{')) {
            quickCheck = '{';
          } else if (patternStr.includes('<') || patternStr.includes('\\<')) {
            const tagMatch = patternStr.match(/<(\w+)/);
            if (tagMatch) {
              quickCheck = `<${tagMatch[1]}`;
            }
          } else if (patternStr.includes('\\[') || patternStr.includes('\\]')) {
            quickCheck = '[';
          } else if (patternStr.includes('\\(') || patternStr.includes('\\)')) {
            quickCheck = '(';
          } else if (patternStr.startsWith('>')) {
            quickCheck = '>';
          } else if (patternStr.startsWith('<')) {
            const firstTagMatch = patternStr.match(/<(\w+)/);
            if (firstTagMatch) {
              quickCheck = `<${firstTagMatch[1]}`;
            }
          } else {
            quickCheck = patternStr.substring(0, Math.min(15, patternStr.length));
          }

          return {
            id: rule.id,
            pattern,
            name: rule.scriptName,
            quickCheck,
          };
        } catch (e) {
          console.warn(`无效的正则表达式: ${patternStr}`, e);
          return null;
        }
      })
      .filter(Boolean) as { id: string; pattern: RegExp; name: string; quickCheck: string }[];
  };

  // 已存在的正则名称缓存
  const existingRegexNames = new Set<string>();

  // 检测消息中是否包含需要处理的表达式
  const detectNeededRegexes = (messageContent: string, registeredRegexIds: Set<string>): string[] => {
    if (!messageContent || messageContent.length < 3) {
      return [];
    }

    // 如果所有正则都已注册，直接返回空
    const patterns = extractDetectionPatterns();
    if (registeredRegexIds.size >= patterns.length) {
      return [];
    }

    const neededIds: string[] = [];

    for (const { id, pattern, name, quickCheck } of patterns) {
      // 跳过已注册的正则
      if (registeredRegexIds.has(id)) {
        continue;
      }

      // 跳过酒馆中已存在的同名正则
      if (existingRegexNames.has(name)) {
        registeredRegexIds.add(id);
        continue;
      }

      // 快速预检查
      if (quickCheck && !messageContent.includes(quickCheck)) {
        continue;
      }

      try {
        pattern.lastIndex = 0;
        if (pattern.test(messageContent)) {
          neededIds.push(id);
          console.info(`自适应正则: 检测到 ${name}，准备注册`);
        }
      } catch (e) {
        // 忽略正则测试错误
      }
    }

    return neededIds;
  };

  // 批量注册正则
  const registerMultipleRegexes = async (ids: string[], registeredRegexIds: Set<string>): Promise<void> => {
    if (ids.length === 0) return;

    const allRules = parseRegexRules();
    const rulesToRegister: TavernRegex[] = [];

    for (const id of ids) {
      const rule = allRules.find((r: TavernRegex) => r.id === id);
      if (rule && !registeredRegexIds.has(rule.id)) {
        registeredRegexIds.add(rule.id);
        rulesToRegister.push(rule);
      }
    }

    if (rulesToRegister.length === 0) return;

    // 批量更新酒馆正则
    await window.TavernHelper.updateTavernRegexesWith((regexes: TavernRegex[]) => {
      const registeredNames = rulesToRegister.map(r => r.script_name);
      const filtered = regexes.filter((r: TavernRegex) => !registeredNames.includes(r.script_name));
      return [...filtered, ...rulesToRegister];
    });

    // 保存到缓存
    saveCachedRegexIds(registeredRegexIds);

    console.info(`自适应正则: 已批量注册 ${rulesToRegister.length} 条规则`);
  };

  // 处理消息
  const processMessage = async (messageId: number): Promise<void> => {
    try {
      const messages = getChatMessages(messageId);
      if (!messages || messages.length === 0) return;

      const messageContent = messages[0].message;
      const registeredRegexIds = getCachedRegexIds();
      const neededIds = detectNeededRegexes(messageContent, registeredRegexIds);

      if (neededIds.length > 0) {
        await registerMultipleRegexes(neededIds, registeredRegexIds);
      }
    } catch (error) {
      console.error('自适应正则处理出错:', error);
    }
  };

  // 监听消息接收事件
  eventOn(tavern_events.MESSAGE_RECEIVED, async (messageId: number) => {
    await processMessage(messageId);
  });

  // 监听消息编辑事件
  eventOn(tavern_events.MESSAGE_EDITED, async (messageId: number) => {
    await processMessage(messageId);
  });

  // 切换聊天时检查缓存与实际正则的差异（带防抖）
  let syncTimeout: ReturnType<typeof setTimeout> | null = null;
  let lastChatId: string | null = null;

  const syncRegexOnChatChange = async (newChatId: string): Promise<void> => {
    // 忽略相同的聊天ID
    if (lastChatId === newChatId) return;
    lastChatId = newChatId;

    // 防抖：延迟执行，避免频繁触发
    if (syncTimeout) {
      clearTimeout(syncTimeout);
    }

    syncTimeout = setTimeout(async () => {
      try {
        const cachedIds = getCachedRegexIds();
        const allRules = parseRegexRules();

        // 获取当前酒馆中已注册的正则
        const currentRegexNames = new Set<string>();
        await window.TavernHelper.updateTavernRegexesWith((regexes: TavernRegex[]) => {
          regexes.forEach(r => currentRegexNames.add(r.script_name));
          return regexes;
        });

        // 需要移除的：缓存中有但正则已不存在的（通过检查规则是否存在）
        const toRemove: string[] = [];
        for (const id of cachedIds) {
          const rule = allRules.find(r => r.id === id);
          if (!rule) {
            // 规则已不存在于配置中
            toRemove.push(id);
          } else if (!currentRegexNames.has(rule.script_name)) {
            // 规则存在但未在酒馆中注册（可能被其他方式删除了）
            toRemove.push(id);
          }
        }

        // 需要重新注册的：缓存中有ID，但酒馆中实际没有注册对应的正则
        const toReregister: string[] = [];
        for (const id of cachedIds) {
          if (toRemove.includes(id)) continue;

          const rule = allRules.find(r => r.id === id);
          if (rule && !currentRegexNames.has(rule.script_name)) {
            toReregister.push(id);
          }
        }

        // 执行移除
        if (toRemove.length > 0) {
          const removeNames = toRemove
            .map(id => allRules.find(r => r.id === id)?.script_name)
            .filter(Boolean) as string[];

          if (removeNames.length > 0) {
            await window.TavernHelper.updateTavernRegexesWith((regexes: TavernRegex[]) => {
              return regexes.filter((r: TavernRegex) => !removeNames.includes(r.script_name));
            });
            console.info(`自适应正则: 移除了 ${removeNames.length} 条不存在的规则`);
          }

          // 更新缓存
          toRemove.forEach(id => cachedIds.delete(id));
        }

        // 执行重新注册
        if (toReregister.length > 0) {
          await registerMultipleRegexes(toReregister, cachedIds);
          console.info(`自适应正则: 重新注册了 ${toReregister.length} 条规则`);
        }

        console.info(`自适应正则: 聊天切换同步完成，缓存 ${cachedIds.size} 条规则`);
      } catch (error) {
        console.error('自适应正则聊天切换同步出错:', error);
      }
    }, 500); // 500ms 防抖延迟
  };

  // 监听聊天切换事件
  eventOn(tavern_events.CHAT_CHANGED, async (newChatId: string) => {
    console.info(`自适应正则: 检测到聊天切换到 ${newChatId}`);
    await syncRegexOnChatChange(newChatId);
  });

  const enabledRules = regexData.filter((r: any) => !r.disabled).length;
  console.info(`自适应正则: 准备了 ${enabledRules} 条规则用于检测`);

  // 初始化时获取酒馆中已存在的正则，并同步缓存
  await (async () => {
    try {
      await window.TavernHelper.updateTavernRegexesWith((regexes: TavernRegex[]) => {
        regexes.forEach(r => existingRegexNames.add(r.script_name));
        return regexes;
      });

      // 首次加载时同步一次缓存
      await syncRegexOnChatChange(SillyTavern.getCurrentChatId());
    } catch (e) {
      // 忽略初始化错误
    }
  })();

  // 卸载时移除所有已注册的正则
  $(window).on('pagehide', async () => {
    await (async () => {
      // 获取当前缓存
      const registeredRegexIds = getCachedRegexIds();
      if (registeredRegexIds.size === 0) return;

      const allRules = parseRegexRules();
      const registeredNames = Array.from(registeredRegexIds)
        .map(id => allRules.find((r: TavernRegex) => r.id === id)?.script_name)
        .filter(Boolean) as string[];

      await window.TavernHelper.updateTavernRegexesWith((regexes: TavernRegex[]) => {
        return regexes.filter((r: TavernRegex) => !registeredNames.includes(r.script_name));
      });

      console.info(`自适应正则: 已移除 ${registeredNames.length} 条规则`);

      // 清除缓存
      saveCachedRegexIds(new Set());
    })();
    console.info('自适应正则脚本已卸载');
  });
});
